body {
    font-family: 'Roboto', sans-serif;
    font-size: 1.6rem;
    font-weight: 400;
    color: $color-grey;
    letter-spacing: .2rem; }

.title {
    text-transform: uppercase;
    letter-spacing: 1rem;
    font-weight: 800; }

.title-first {
    color: #FFFFFF;
    &__main {
        display: block;
        font-size: 5rem;
        line-height: 6rem;
        margin-bottom: 1rem;
        animation: leftToRight 2s ease .8s;
        backface-visibility: hidden;
        animation-fill-mode: backwards; }
    &__secondary {
        display: block;
        font-size: 4rem;
        letter-spacing: .5rem;
        font-weight: 300;
        line-height: 4.8rem;
        margin-bottom: 6.5rem;
        animation: RightToLeft 2s ease .8s;
        backface-visibility: hidden;
        animation-fill-mode: backwards; } }
